<template>
  <div class="rand-page">
    <section class="rand-hero">
      <div class="pill">
        <span>Инструмент</span>
        <strong>Колесо решений</strong>
      </div>
      <h1>Рандомайзер</h1>
      <p class="lead">
        Когда идей много, а выбрать сложно. Колесо сделает это за вас: случайное число, слово из списка или простой
        ответ «да / нет».
      </p>
    </section>

    <section class="mode-bar">
      <div class="mode-switch">
        <button
          v-for="m in modes"
          :key="m.id"
          class="mode-btn"
          :class="{ active: mode === m.id }"
          @click="mode = m.id"
        >
          {{ m.label }}
        </button>
      </div>
    </section>

    <section class="rand-card">
      <div class="mode-body">
        <template v-if="mode === 'number'">
          <div class="field-row">
            <label>
              От
              <input v-model.number="range.min" type="number" />
            </label>
            <label>
              До
              <input v-model.number="range.max" type="number" />
            </label>
          </div>
          <p class="hint">Укажи границы, и мы выберем любое число внутри диапазона.</p>
        </template>

        <template v-else-if="mode === 'list'">
          <label class="full">
            Вставь варианты через запятую или с новой строки, мы выберем один из них
            <textarea v-model="listInput" rows="4" placeholder="пицца&#10;бургер&#10;роллы"></textarea>
          </label>
        </template>

        <template v-else>
          <p class="hint">Простой рандом: «да» или «нет» — решает за пару секунд.</p>
        </template>

        <div v-if="result" class="result">
          <span class="result-label">Результат:</span>
          <span class="result-value">{{ result }}</span>
        </div>
      </div>
    </section>

    <div class="action-bar">
      <button class="btn primary" @click="generate">Сгенерировать</button>
    </div>

    <section class="benefits">
      <h2>Зачем этот рандомайзер</h2>Выбор без споров
      <div class="grid three">
        <article class="card">
          <h3>Универсально</h3>
          <p>Числа, список слов или простой «да / нет» — под любую задачу, от выбора фильма до жеребьёвки.</p>
        </article>
        <article class="card">
          <h3>Честно</h3>
          <p>crypto.getRandomValues вместо подбрасываний монет и споров. Результат максимально случайный.</p>
        </article>
        <article class="card">
          <h3>Быстро</h3>
          <p>Пара кликов — и готово. Можно крутить бесконечно, пока не найдёте идеальный вариант.</p>
        </article>
      </div>
    </section>

    <section class="faq">
      <div class="faq-text">
        <h2>FAQ</h2>
        <p>Нужен только браузер: всё работает сразу, без регистрации и сторонних сервисов.</p>
        <p>Для списков можно вставлять варианты в столбик или через запятую — мы почистим лишние пробелы.</p>
        <p>Результат не хранится и не кэшируется: каждый клик — новый случайный выбор.</p>
      </div>
    </section>

    <section class="cta">
      <div class="cta-box card">
        <div>
          <p class="seo-kicker">Выбор без споров</p>
          <h3>Запусти колесо и реши за 5 секунд</h3>
        </div>
        <button class="btn primary" @click="generate">Сгенерировать снова</button>
      </div>
    </section>
  </div>
</template>

<script setup>
import { computed, reactive, ref } from 'vue'

const modes = [
  { id: 'number', label: 'Число' },
  { id: 'list', label: 'Список' },
  { id: 'yesno', label: 'Да / нет' }
]

const mode = ref('number')
const range = reactive({ min: 1, max: 100 })
const listInput = ref('')
const result = ref('')

const cleanedList = computed(() =>
  listInput.value
    .split(/[\n,]/)
    .map((item) => item.trim())
    .filter(Boolean)
)

function randomIndex(max) {
  const buf = new Uint32Array(1)
  crypto.getRandomValues(buf)
  return buf[0] % max
}

function generate() {
  if (mode.value === 'number') {
    const min = Number.isFinite(range.min) ? range.min : 1
    const max = Number.isFinite(range.max) ? range.max : 100
    const low = Math.min(min, max)
    const high = Math.max(min, max)
    const span = high - low + 1
    const idx = randomIndex(span)
    result.value = String(low + idx)
    return
  }

  if (mode.value === 'list') {
    if (!cleanedList.value.length) {
      result.value = 'Добавь хотя бы один вариант'
      return
    }
    const choice = cleanedList.value[randomIndex(cleanedList.value.length)]
    result.value = choice
    return
  }

  const yesNo = ['Да', 'Нет']
  result.value = yesNo[randomIndex(yesNo.length)]
}
</script>

